:root {
    --bouncy-easing: cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

html,
body {
    height: 100vh;
    width: 100%;
    overflow: auto;
}

.App {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
    height: 100vh;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding-bottom: 100px;
    overflow: hidden;
}

.table {
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-align: center;
    position: relative;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    padding-right: 9%;
    max-width: 500px;
    margin: 0 auto;
}

.table__col {
    width: 9%;
    position: relative;
    margin: 0;
    border: 0;

    border-radius: 50%;
    background-color: #e2e2e2;
}

.table__col:before {
    content: '';
    display: block;
    padding-top: 50%;
}

.table__head {
    color: #A7AEAB;
    font-weight: normal;
    font-size: .75rem;
    background-color: #fff;
}

.number {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    text-align: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 2rem;
}

.number--byOne {
    background-color: aquamarine;
}

.number--sameNumbers {
    background-color: lightblue;
}

.display {
    text-align: center;
}

.selected {
    text-align: center;
    font-size: 1rem;
    min-height: 1rem;
    margin: .5rem 0;
}

.number--selected {
    background-image: url('data:image/svg+xml,\
          <svg xmlns="http://www.w3.org/2000/svg" width="50%" height="50%" fill-opacity="1">\
            <ellipse cx="50%" cy="50%" rx="25%" ry="25%" fill-color="red"/>\
          </svg>');
}

.solution {
    min-width: 3rem;
    height: 1.2rem;
    display: inline-block;
    line-height: 1rem;
    text-align: center;
    border-radius: .5rem;
    background-color: #e2e2e2;
    margin: 0 .25rem;
}

.tip:after {
    content: '';
    display: block;
    border-radius: 50%;
    width: 50%;
    height: 50%;
    background-color: crimson;
    position: absolute;
    top: 25%;
    left: 25%;
    -webkit-animation: tipAnimation 1s linear infinite;
            animation: tipAnimation 1s linear infinite;
    pointer-events: none;
}

@-webkit-keyframes tipAnimation {
    0%{
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: .8;
    }
    100% {
        -webkit-transform: scale(3);
                transform: scale(3);
        opacity: .1;
    }
}

@keyframes tipAnimation {
    0%{
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: .8;
    }
    100% {
        -webkit-transform: scale(3);
                transform: scale(3);
        opacity: .1;
    }
}


