.task {
    margin-bottom: .25rem;
}
.correct {
    /* @keyframes duration | timing-function | delay |
iteration-count | direction | fill-mode | play-state | name */
    -webkit-animation: correctAnimation .3s var(--bouncy-easing) .25s;
            animation: correctAnimation .3s var(--bouncy-easing) .25s;
}
.fail {
    -webkit-animation: failAnimation .2s linear;
            animation: failAnimation .2s linear;
}
@-webkit-keyframes correctAnimation {
    0%, 100% {
        color: currentColor;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    25% {
        -webkit-transform: scale(.8);
                transform: scale(.8);
    }
    50% {
        color: #bada55;
    }
    75% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
}
@keyframes correctAnimation {
    0%, 100% {
        color: currentColor;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    25% {
        -webkit-transform: scale(.8);
                transform: scale(.8);
    }
    50% {
        color: #bada55;
    }
    75% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
}
@-webkit-keyframes failAnimation {
    0%, 100% {
        color: currentColor;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    25%, 75% {
        -webkit-transform: translateX(-5%);
                transform: translateX(-5%);
    }
    50% {
        color: hotpink;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    75% {
        -webkit-transform: translateX(5%);
                transform: translateX(5%);
    }
}
@keyframes failAnimation {
    0%, 100% {
        color: currentColor;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    25%, 75% {
        -webkit-transform: translateX(-5%);
                transform: translateX(-5%);
    }
    50% {
        color: hotpink;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    75% {
        -webkit-transform: translateX(5%);
                transform: translateX(5%);
    }
}


